import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import DeleteDialog from './Dialogs/DeleteDialog';

const ITEM_HEIGHT = 48;

class EnhancedMenu extends React.Component {
  state = {
    anchorEl: null,
    options: [
      {
        jsx: <DeleteDialog
          positiveHandler={e => null}
        />,
        onClick: e => null
      }
    ]
  };

  handleClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);
    const { options = [
      { jsx: <h4>Hello</h4>, onClick: e => console.log("hello clicked") },
      { jsx: <h4>Hello 1</h4>, onClick: e => console.log("hello1 clicked") },
    ] } = this.props;
    return (
      <div>
        <IconButton
          aria-label="More"
          aria-owns={open ? 'long-menu' : null}
          aria-haspopup="true"
          onClick={this.handleClick}
        >
          <MoreVertIcon />
        </IconButton>
        <Menu
          id="long-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={this.handleClose}
          PaperProps={{
            style: {
              //   maxHeight: ITEM_HEIGHT * 4.5,
              width: 200,
            },
          }}
        >
          {options.map(option => (
            <MenuItem key={Math.random()} onClick={e => { this.handleClose(e); option.onClick(e); }}>
              {option.jsx}
            </MenuItem>
          ))}
        </Menu>
      </div>
    );
  }
}

export default EnhancedMenu;


